import React from 'react';
import logo from './images/logo_footer.svg';
import mark from './images/footer_footer.svg';
import styles from './Footer.module.scss';
import Partners from "../Partners/Partners";

const initFooterLinks = [
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: ' ',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: ' ',
        link: '#'
    },
    {
        name: ' ',
        link: '#'
    },
    {
        name: '.',
        link: '#'
    },
    {
        name: '',
        link: '#'
    },
    {
        name: ' .',
        link: '#'
    },
    {
        name: 'Lark+',
        link: '#'
    },
    {
        name: ' ',
        link: '#'
    },
    {
        name: ' ',
        link: '#'
    },
];

const Footer = () => {
    const firstBlockOfLinks = initFooterLinks.slice(0,5);
    const secondBlockOfLinks = initFooterLinks.slice(5,10);
    const thirdBlockOfLinks = initFooterLinks.slice(10);

    return (
        <footer className={styles.footer}>
            <img src={logo} alt=' '/>
            <Partners/>
            <div className={styles.footer__body}>
                <div>
                    <h3> 8/3</h3>
                    <p>, . , 8, . 3  </p>
                </div>
                <div className={styles.footer__links}>
                    <ul>
                        {firstBlockOfLinks.map((el, index) => (
                            <li key={index}>
                                <a href={el.link} target='_blank' rel="noreferrer noopener">{el.name}</a>
                            </li>
                        ))}
                    </ul>
                    <ul>
                        {secondBlockOfLinks.map((el, index) => (
                            <li key={index}>
                                <a href={el.link} target='_blank' rel="noreferrer noopener">{el.name}</a>
                            </li>
                        ))}
                    </ul>
                    <ul>
                        {thirdBlockOfLinks.map((el, index) => (
                            <li key={index}>
                                <a href={el.link} target='_blank' rel="noreferrer noopener">{el.name}</a>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
            <div className={styles.footer__footer}>
                <div>
                    <p> , 2021</p>
                    <p><a href={'#'}>  </a></p>
                </div>
                <div>
                    <div>
                        <img src={mark} alt=''/>
                       <p>   shishki.collective</p>
                    </div>
                    <span>     <a href={'#'}> . </a></span>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
