import styles from "./Head.module.scss";
import audience from "../../images/audience.jpg";

function Head() {
  return (
    <section className={styles.section}>
      <h1 className={styles.title}>. </h1>
      <p className={styles.subtitle}>
                
        .
      </p>
      <img
        src={audience}
        alt="    ."
        className={styles.image}
      />
    </section>
  );
}

export default Head;
